import { DatePipe, NgClass } from '@angular/common';
import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { DataBin } from '@biz-nest/commerce-client';
import {
  AppNameService,
  EventbusService,
  ListComposeAdminPanel,
  MainBarTitleService,
  NestedDataPipe, WindowService,
  scrollToTop
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { AdminDateFilterFieldComponent } from '../../misc/admin-date-filter-field/admin-date-filter-field.component';
import {
  AdminListDropdownComponent
} from '../../misc/admin-dropdown/admin-list-dropdown.component';
import { AdminModalComponent } from '../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminPaginatorComponent } from '../../misc/admin-paginator/admin-paginator.component';
import { AdminSearchFieldComponent } from '../../misc/admin-search-field/admin-search-field.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';

@Component({
  selector: 'app-data-bin-list',
  standalone: true,
  imports: [
    RouterModule,
    DatePipe,
    NestedDataPipe,
    AdminModalComponent,
    AdminPaginatorComponent,
    AdminListDropdownComponent,
    AdminPageContentComponent,
    NgClass,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent
  ],
  templateUrl: './data-bin-list.component.html',
  styleUrl: './data-bin-list.component.scss'
})
export class DataBinListComponent
implements OnInit, OnDestroy {
  isRestoreMany = false;
  dest$ = new Subject();
  cleanup?: () => void;
  sortStates = {
    id: 'asc',
    info: 'asc',
    date: 'asc'
  };

  listCompose = new ListComposeAdminPanel<DataBin>(
    DataBin,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'Restore',
      icon: 'edit',
      event: (dataBin: DataBin) => this.restore(dataBin)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (dataBin: DataBin) => this.listCompose
        .openDeleteOneModal(
          dataBin._id,
          'Delete Data Bin',
          'Are You Sure You Want To Delete This Data Bin?'
        )
    }
  ];

  constructor(
    private windowService: WindowService,
    private router: Router,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private toastr: ToastrService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Track Deletes';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and view user accounts for your organization to ensure effective tracking and oversight.'
    });
    this.listCompose.getDataArr();
    scrollToTop(this.windowService);
  }

  async restore(dataBin: DataBin) {
    await dataBin.restore();
    this.listCompose.selections = [];
    this.listCompose.offset -= this.listCompose.limit;
    this.listCompose.getDataArr();
  }

  async restoreMany() {
    if (this.isRestoreMany) {
      this.toastr.error('Wait for the current restore process to complete');

      return;
    }

    this.isRestoreMany = true;

    const response = await DataBin.restoreMany({ _ids: this.listCompose.selections });

    this.isRestoreMany = false;
    if (response.success) {
      this.listCompose.selections = [];
      this.listCompose.offset -= this.listCompose.limit;
      this.listCompose.getDataArr();
    }
  }

  viewActivity(parent: string, header: string) {
    this.navigateByBus(['/activity/'], 'viewActivity', {
      parent,
      header
    });
  }

  toggleSelectAll() {
    if (!this.listCompose.limit) {
      return;
    }

    if (this.listCompose.selections && this.listCompose.selections.length <= 0) {
      const startPos = this.listCompose.limit * (this.listCompose.currentIndex - 1);
      const endPos = startPos + this.listCompose.limit;
      const selectedIds = this.listCompose.dataArr.slice(startPos, endPos).map(item => item._id);

      this.listCompose.selections = selectedIds;
    } else {
      this.listCompose.selections = [];
    }
  }

  ngOnDestroy() {
    this.cleanup?.();
    this.listCompose.cleanup?.();
  }
}

